.Loader.container{
  text-align: center;
  padding-top: 20px;
}

.myTooltip {
    position: relative;
}
  
.myTooltip .myTooltiptext {
    visibility: hidden;
    background-color: #f4f4ff;
    color: #000;

    border-color: #888;
    border-style: dashed;
    border-radius: 2px;
    border-width: 1px;

    text-align: center;
    padding: 5px 10px;
    
    position: absolute;
    z-index: 10;

    margin-bottom: 10px;
}

.myTooltip .myTooltiptext:before {
    content: "";
    border-left: 9px solid transparent;
    border-right: 9px solid transparent;
    border-bottom: 9px solid #777;
    top: -9px;
    position: absolute;
    left: 45%,
}

.myTooltip:hover .myTooltiptext {
    visibility: visible;
}
.srb-pane-title{
    color:#fff;
    background-color:#0070bf;
    border-color:#0070bf;
}
.scheduler-box{
    background-color: #fff;
    border-color: #888;
    border-style: solid;
    border-radius: 2px;
    border-width: 0px;
    padding: 20px;
}

.srb-dropbox {
    margin-bottom: 10px;
}
.srb-markdown p {
    margin-top:10px;
    margin-bottom:5px;
}

.srb-markdown table, th, td, tr {
    border:solid black 1px;
    padding: 0px 7px 0px 7px;
}

.srb-markdown table {
    margin-bottom: 10px;
    margin-left: 45px;
}
.navbar {
    background-color: rgb(73, 73, 73);
}
.srb-footer {
   position: fixed;
   left: 0;
   bottom: 0;
   width: 100%;
   color: white;
   text-align: center;
}

.srb-footer .content {
    font-size: 15px;
    padding-top: 10px;
    padding-bottom:10px;
}

.srb-footer hr {
    margin-top: 10px;
}


  
@media screen and (max-width: 800px) {
    .srb-footer .content {
        font-size: 12px;
        padding-top: 8px;
        padding-bottom:8px;
    }
}

@media screen and (max-width: 580px) {
    .srb-footer .status-dot {
        visibility: hidden;
        height:0px;
        width:0px;
        padding:0px;
    }
}

@media screen and (max-width: 400px) {
    .srb-footer .content {
        font-size: 10px;
        padding-top: 4px;
        padding-bottom:4px;
    }
}
